.ytp-drawer {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	width: 350px;
	text-align: center;
	overflow: hidden;
	z-index: 65;
	transition: width .25s cubic-bezier(0,0,0.2,1),bottom .25s cubic-bezier(0,0,0.2,1)
}

.ytp-autohide .ytp-drawer {
